@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: "Work Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-width: 620px;
  height: 60px;
  margin: 0 auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.103);
}

.logo-container {
  width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

.logo {
  width: 50px;
  height: auto;
  -webkit-filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg)
    brightness(102%) contrast(102%);
          filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg)
    brightness(102%) contrast(102%);
}

.option-container {
  width: 70%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}

.header-icon {
  margin-right: 4px;
  font-size: 15px;
}

.option-container nav {
  margin-right: 10px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 8px 12px;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
}

.option-container nav:last-child {
  margin-right: 0;
}

.control-panel {
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 40px;
}

.control-panel span {
  margin-right: 10px;
  padding: 5px 12px;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
}

.toggle-bg-color {
  background-color: rgba(0, 0, 0, 0.15);
}

.timer {
  font-family: "Source Serif Pro", serif;
  font-weight: bold;
  font-size: 8rem;
  text-align: center;
  padding: 10px;
}

.start-button-container {
  text-align: center;
  padding-bottom: 30px;
}

.start-button {
  color: rgb(219, 82, 77);
  width: 200px;
  height: 55px;
  border-style: none;
  border-radius: 4px;
  font-size: 1.5rem;
  font-weight: 700;
  cursor: pointer;
  box-shadow: rgb(216 216 216) 0px 6px 0px;
}

.task-pop-up {
  width: 200px;
  background-color: white;
  border-radius: 5px;
  color: black;
  position: absolute;
  left: 265px;
  top: 50px;
  padding: 4px 0px 4px 15px;
  font-size: 0.9rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  z-index: 10;
}

.hide-popup {
  display: none;
}

.task-pop-up-options {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.task-pop-up-icon {
  margin-right: 5px;
}

.add-task-panel {
  border: 2px dashed rgba(255, 255, 255, 0.4);
  background-color: rgba(0, 0, 0, 0.1);
  text-align: center;
  margin: 20px 0;
  opacity: 0.7;
  cursor: pointer;
}
.add-task-panel:hover {
  opacity: 0.9;
}

h4 {
  display: inline-block;
  margin-left: 5px;
}

.add-task-form-container {
  background-color: white;
  color: black;
  border-radius: 8px;
  margin-top: 20px;
}

.add-task-form {
  padding: 10px 20px;
  color: rgb(85, 85, 85);
}

.add-task-form-input {
  display: block;
  border: none;
  height: 40px;
  font-size: 1.5rem;
  font-weight: bold;
  width: 100%;
  color: rgb(85, 85, 85);
  z-index: -1;
}

.add-task-form-option-buttons input {
  width: 75px;
  background-color: rgb(239, 239, 239);
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  color: rgb(85, 85, 85);
  font-size: 16px;
  font-weight: bold;
}

.add-task-option-buttons button {
  height: 30px;
  width: 40px;
  margin-right: 5px;
  border-radius: 4px;
  font-size: 13px;
  border: 1px solid rgb(223, 223, 223);
  box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
  background-color: white;
  color: rgb(85, 85, 85);
}

.add-note-add-project-panel {
  margin: 20px 0;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.add-note-add-project-panel button {
  margin-right: 10px;
  font-size: 15px;
  font-weight: bold;
  -webkit-text-decoration-style: none;
          text-decoration-style: none;
  border: none;
  background-color: white;
  color: rgba(0, 0, 0, 0.4);
  text-decoration: underline;
  cursor: pointer;
}

.cancel-save-container {
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 14px 20px;
  background-color: rgb(239, 239, 239);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.cancel-save-container button {
  border: none;
  font-size: 17px;
  padding: 8px 12px;
  min-width: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 600;
  opacity: 0.8;
  cursor: pointer;
}

.save-btn {
  background-color: rgb(171, 171, 171);
  border: 2px solid rgb(171, 171, 171);
  border-radius: 4px;
  color: white;
  margin-left: 8px;
}

.new-task-tag {
  background-color: white;
  color: rgb(197, 195, 195);
  padding: 0 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 17px;
  border-radius: 8px;
  margin-bottom: 5px;
  border-left: 8px solid black;
}

.check-circle {
  font-size: 25px;
}

.new-task-title {
  width: 80%;
  color: rgb(85, 85, 85);
}

.new-task-number {
  width: 10%;
}

.trash-icon {
  font-size: 18px;
  color: rgb(107, 107, 107);
  cursor: pointer;
}



.task-container {
  width: 480px;
  margin: 0 auto;
  padding-bottom: 30px;
}

.task-header-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 2px solid rgba(255, 255, 255, 0.6);
  margin-bottom: 20px;
  position: relative;
}

.task-header {
  display: inline-block;
  width: 90%;
}

.task-icon {
  font-size: 18px;
  padding: 6px 13px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  cursor: pointer;
}

.homepage-container {
  background-color: rgb(219, 82, 77);
  color: white;
}

.timer-container {
  max-width: 480px;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 0 auto;
  border-radius: 8px;
}

.description-container {
  text-align: center;
  padding: 20px 0;
}

